import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

function Login() {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    username: '',
    password: ''
  });
  const [error, setError] = useState('');

  //   
  const accounts = {
    'comento': { password: 'comento2025', role: 'company', companyId: 'comento', companyName: '' },
    'sk-innovation': { password: 'sk2025', role: 'company', companyId: 'sk-innovation', companyName: 'SK' },
    'dongseobaljeon': { password: 'dongse02025', role: 'company', companyId: 'kewespo', companyName: '' },
    'admin': { password: 'elephant2025', role: 'admin', companyId: null, companyName: '' }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    setError('');
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const account = accounts[formData.username];

    if (!account) {
      setError('  .');
      return;
    }

    if (account.password !== formData.password) {
      setError('  .');
      return;
    }

    //   - localStorage   
    const userInfo = {
      username: formData.username,
      role: account.role,
      companyId: account.companyId,
      companyName: account.companyName
    };
    localStorage.setItem('userInfo', JSON.stringify(userInfo));

    //  
    navigate('/dashboard');
  };

  return (
    <div className="main-content">
      <div style={{
        maxWidth: '500px',
        margin: '4rem auto',
        padding: '3rem',
        background: 'white',
        borderRadius: '1rem',
        boxShadow: '0 4px 6px -1px rgba(0, 0, 0, 0.1)'
      }}>
        {/*  */}
        <div style={{
          textAlign: 'center',
          marginBottom: '2rem'
        }}>
          <div style={{ fontSize: '4rem', marginBottom: '1rem' }}></div>
          <h1 style={{
            fontSize: '2rem',
            fontWeight: '700',
            color: 'var(--color-primary)',
            marginBottom: '0.5rem'
          }}>
            
          </h1>
          <p style={{ color: 'var(--color-text-light)' }}>
            ESG  
          </p>
        </div>

        {/*   */}
        <form onSubmit={handleSubmit}>
          <div style={{ marginBottom: '1.5rem' }}>
            <label style={{
              display: 'block',
              marginBottom: '0.5rem',
              fontWeight: '600',
              color: 'var(--color-text-dark)'
            }}>
              
            </label>
            <input
              type="text"
              name="username"
              value={formData.username}
              onChange={handleInputChange}
              placeholder=" "
              style={{
                width: '100%',
                padding: '0.75rem',
                border: '1px solid var(--color-border)',
                borderRadius: '0.5rem',
                fontSize: '1rem',
                outline: 'none'
              }}
              required
            />
          </div>

          <div style={{ marginBottom: '1.5rem' }}>
            <label style={{
              display: 'block',
              marginBottom: '0.5rem',
              fontWeight: '600',
              color: 'var(--color-text-dark)'
            }}>
              
            </label>
            <input
              type="password"
              name="password"
              value={formData.password}
              onChange={handleInputChange}
              placeholder=" "
              style={{
                width: '100%',
                padding: '0.75rem',
                border: '1px solid var(--color-border)',
                borderRadius: '0.5rem',
                fontSize: '1rem',
                outline: 'none'
              }}
              required
            />
          </div>

          {error && (
            <div style={{
              padding: '0.75rem',
              marginBottom: '1rem',
              backgroundColor: '#FEE2E2',
              color: '#DC2626',
              borderRadius: '0.5rem',
              fontSize: '0.875rem'
            }}>
              {error}
            </div>
          )}

          <button
            type="submit"
            className="btn btn-primary"
            style={{
              width: '100%',
              marginBottom: '1rem'
            }}
          >
            
          </button>
        </form>

        {/*   */}
        <div style={{
          textAlign: 'center',
          marginTop: '1.5rem',
          paddingTop: '1.5rem',
          borderTop: '1px solid var(--color-border)'
        }}>
          <p style={{ color: 'var(--color-text-light)', marginBottom: '0.5rem' }}>
             ?
          </p>
          <button
            onClick={() => navigate('/signup')}
            className="btn btn-outline"
            style={{
              width: '100%'
            }}
          >
            
          </button>
        </div>

        {/*    */}
        <div style={{
          marginTop: '2rem',
          padding: '1rem',
          backgroundColor: 'var(--color-bg-light)',
          borderRadius: '0.5rem',
          fontSize: '0.875rem',
          color: 'var(--color-text-light)'
        }}>
          <strong style={{ display: 'block', marginBottom: '0.5rem', color: 'var(--color-text-dark)' }}>
             :
          </strong>
          <div>: comento / comento2025</div>
          <div>: sk-innovation / sk2025</div>
          <div>: dongseobaljeon / dongse02025</div>
          <div>: admin / elephant2025</div>
        </div>
      </div>
    </div>
  );
}

export default Login;
